.mint-button {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border-radius: 4px;
    border: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: inherit;
    display: block;
    font-size: 18px;
    height: 41px;
    outline: 0;
    overflow: hidden;
    position: relative;
    text-align: center
}
.mint-button::after {
    background-color: #000;
    content: " ";
    opacity: 0;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    position: absolute
}
.mint-button:not(.is-disabled):active::after {
    opacity: .4
}
.mint-button.is-disabled {
    opacity: .6
}
.mint-button-icon {
    vertical-align: middle;
    display: inline-block
}
.mint-button--default {
    color: #656b79;
    background-color: #f6f8fa;
    -webkit-box-shadow: 0 0 1px #b8bbbf;
            box-shadow: 0 0 1px #b8bbbf
}
.mint-button--default.is-plain {
    border: 1px solid #5a5a5a;
    background-color: transparent;
    -webkit-box-shadow: none;
            box-shadow: none;
    color: #5a5a5a
}
.mint-button--primary {
    color: #fff;
    background-color: #26a2ff
}
.mint-button--primary.is-plain {
    border: 1px solid #26a2ff;
    background-color: transparent;
    color: #26a2ff
}
.mint-button--danger {
    color: #fff;
    background-color: #ef4f4f
}
.mint-button--danger.is-plain {
    border: 1px solid #ef4f4f;
    background-color: transparent;
    color: #ef4f4f
}
.mint-button--large {
    display: block;
    width: 100%
}
.mint-button--normal {
    display: inline-block;
    padding: 0 12px
}
.mint-button--small {
    display: inline-block;
    font-size: 14px;
    padding: 0 12px;
    height: 33px
}

@font-face {font-family: "mintui";
  src: url(data:application/x-font-ttf;base64,)
}

.mintui {
  font-family:"mintui" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}

.mintui-search:before { content: "\E604"; }

.mintui-more:before { content: "\E601"; }

.mintui-back:before { content: "\E600"; }

.mintui-field-error:before { content: "\E605"; }

.mintui-field-warning:before { content: "\E608"; }

.mintui-success:before { content: "\E602"; }

.mintui-field-success:before { content: "\E609"; }





























.mint-toast {
    position: fixed;
    max-width: 80%;
    border-radius: 5px;
    background: rgba(0, 0, 0, 0.7);
    color: #fff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    z-index: 1000;
    -webkit-transition: opacity .3s linear;
    transition: opacity .3s linear
}
.mint-toast.is-placebottom {
    bottom: 50px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0)
}
.mint-toast.is-placemiddle {
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%)
}
.mint-toast.is-placetop {
    top: 50px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0)
}
.mint-toast-icon {
    display: block;
    text-align: center;
    font-size: 56px
}
.mint-toast-text {
    font-size: 14px;
    display: block;
    text-align: center
}
.mint-toast-pop-enter, .mint-toast-pop-leave-active {
    opacity: 0
}






















